@charset "UTF-8";

/* 공통 */
body {
	background-color: #edeef0;
}

.skip-navigation {
	position: relative;
	z-index: 1000;
}

.skip-navigation a {
	display: block;
	overflow: hidden;
	z-index: 1000;
	width: 1px;
	height: 1px;
	margin-bottom: -1px;
	color: #000;
	text-align: center;
}

.skip-navigation a:focus,
.skip-navigation a:active {
	width: auto;
	height: auto;
	padding: 10px 0;
	background-color: #eee;
	font-size: 14px;
}

.livechat {
	overflow: hidden;
	position: relative;
	font-size: 16px;
	line-height: 1.4;
}

.ico-history,
.ico-prev,
.img-thumb,
.ico-quick-menu,
.ico-send,
.ico-telephone,
.form-type .c-ick .label:before,
.form-type .c-ick input[type=checkbox]:checked+.label:before,
.form-type .btn-trigger,
.list-chat-history .item-chat-history:after,
.chat-history .bg-badge,
.popup.chat-history .btn-close .ico-close,
.popup.counsel-guide .btn-close .ico-close,
.popup.chat-product-detail .btn-close .ico-close,
.popup.counsel-form .btn-close .ico-close,
.popup.card-type-detail .btn-close .ico-close,
.popup.popup-tgift .btn-close .ico-close {
	display: inline-block;
	overflow: hidden;
	background-image: url(../img/livechat/spr_livechat@2x.png);
	background-repeat: no-repeat;
	background-size: 125px auto;
	text-indent: -9999px;
	vertical-align: top;
}

.livechat .btn-item[disabled] {
	color: #c0c0c0 !important;
	cursor: auto;
}

.livechat .btn-text[disabled] {
	color: #c0c0c0 !important;
	border-color: #c0c0c0;
	cursor: auto;
}

/* 상단 */
.chat-header {
	position: absolute;
	top: 0;
	z-index: 100;
	width: 100%;
}

.chat-header .inner {
	display: table;
	position: relative;
	width: 100%;
	height: 60px;
	border-bottom: 1px solid #e1e1e1;
	background-color: #fff;
	text-align: center;
	table-layout: fixed;
}

.chat-header .btn-chat-prev {
	position: absolute;
	top: 0;
	left: 0;
	width: 48px;
	height: 100%;
}

.chat-header .ico-prev {
	width: 24px;
	height: 24px;
	background-position-x: -68px;
}

.chat-header .title {
	display: table-cell;
	width: 100%;
	padding: 0 60px;
	vertical-align: middle;
	font-size: 18px;
	font-weight: 400;
}

.chat-header .group-btn {
	position: absolute;
	top: 0;
	right: 0;
	height: 100%;
}

.chat-header .btn-counsel-history {
	width: 48px;
	height: 100%;
}

.chat-header .ico-history {
	width: 24px;
	height: 24px;
	background-position-x: -43px;
}

/* 본문 */
.chat-contents {
	overflow-x: hidden;
	overflow-y: auto;
	-webkit-overflow-scrolling: touch;
	position: absolute;
	top: 60px;
	right: 0;
	bottom: 50px;
	left: 0;
	width: 100%;
	z-index: 1;
	/* padding: 60px 0 50px; */
}

.chat-dimmed {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	z-index: 9999;
	background-color: rgba(0, 0, 0, 0.2);
}

.chat-dimmed.on {
	display: block;
}

.chat-contents .inner {
	padding-bottom: 56px;
}

.chat-contents .notice {
	padding-top: 6px;
	text-align: center;
}

.chat-contents .entry-time-stamp {
	font-size: 14px;
	color: #888;
}

.chat-contents .reference {
	padding: 0 10px;
	font-size: 12px;
	color: #aaa;
}

.notice .entry-time-stamp+.reference {
	margin-top: 10px;
}

.message-group {
	position: relative;
	color: #333;
	font-weight: 300;
	word-break: normal;
	word-wrap: break-word;
}

.message-group,
.messsage+.message,
.message-time-stamp+.message {
