/* 하단 */
.chat-input-area {
	position: absolute;
	bottom: 0;
	z-index: 100;
	width: 100%;
}

.chat-input-area .inner {
	display: table;
	position: relative;
	z-index: 20;
	/* table-layout: fixed; */
	width: 100%;
	padding: 4px 13px 4px 18px;
	background-color: #fff;
	-webkit-box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0);
	box-shadow: 0 0 18px 0 rgba(0, 0, 0, 0.1), 0 1px 2px 0 rgba(0, 0, 0, 0);
}

.chat-input-area .inner>div {
	display: table-cell;
	vertical-align: bottom;
}

.chat-input-area .input-area {
	padding: 11px 0;
	border-radius: 17px;
	background-color: #f7f7f7;
}

.chat-input-area .input-message {
	display: block;
	width: 100%;
	max-height: 80px;
	min-height: 20px;
	height: 20px;
	padding: 0 15px;
	border: 0;
	background-color: #f7f7f7;
	line-height: 20px;
	outline: 0;
	-webkit-overflow-scrolling: touch;
}

.chat-input-area .input-message::-webkit-input-placeholder {
	color: #d1d1d1;
	opacity: 1;
}

.chat-input-area .input-message::-moz-placeholder {
	color: #d1d1d1;
	opacity: 1;
}

.chat-input-area .input-message:-ms-input-placeholder {
	color: #d1d1d1;
}

.chat-input-area .input-message:-moz-placeholder {
	color: #d1d1d1;
}

.chat-input-area .btn-area {
	width: 40px;
	padding-left: 14px;
	text-align: center;
}

.chat-input-area .btn-area.send {
	display: none;
	padding-left: 20px;
}

.chat-input-area .btn-area button {
	height: 42px;
	vertical-align: top;
}

.chat-input-area .ico-quick-menu {
	width: 40px;
	height: 40px;
	background-position: -84px -25px;
}

.chat-input-area button.on .ico-quick-menu {
	background-position: -43px -66px;
}

.chat-input-area button[disabled] .ico-quick-menu {
	background-position-x: -43px;
}

.chat-input-area .ico-send {
	width: 34px;
	height: 34px;
	background-position-y: -86px;
}

.input-area.active+.btn-area.quick-menu {
	display: none;
}

.input-area.active~.btn-area.send {
	display: table-cell;
}

.toast .quick-link {
	display: block;
	width: 300px;
	margin: 0 auto 10px;
	padding: 9px 10px 8px;
	border-radius: 18px;
	background-color: rgba(34, 34, 34, 0.6);
	color: #fff;
	font-size: 14px;
}

.global-quick-menu {
	display: none;
	position: relative;
	z-index: 10;
	padding-top: 19px;
	border-top-right-radius: 10px;
	border-top-left-radius: 10px;
	background-color: #f7f7f7;
}

.global-quick-menu:before {
	display: inline-block;
	position: absolute;
	top: 8px;
	left: 50%;
	width: 42px;
	height: 3px;
	border-radius: 1.5px;
	background-color: #d8d8d8;
	-webkit-transform: translateX(-50%);
	transform: translateX(-50%);
	content: '';
}

.list-quick-answer {
	overflow-x: hidden;
	overflow-y: auto;
	max-height: 184px;
	-webkit-overflow-scrolling: touch;
}

.list-quick-answer .btn-answer {
	display: block;
	width: 100%;
	padding: 14px 16px 13px;
	text-align: left;
	font-size: 14px;
	font-weight: 600;
	color: #333;
}

.list-quick-answer .btn-answer:hover,
.list-quick-answer .btn-answer:focus {
	background-color: #f0f0f0;
}

.list-t-gift .thumb-img {
	overflow: hidden;
	position: relative;
	width: 100%;
	padding-top: 100%;
	border-radius: 14px 14px 0 0;
}

.list-t-gift .thumb-img:after {
	position: absolute;
	right: 0;
	bottom: 0;
	left: 0;
	height: 80px;
	background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.03) 100%);
	content: '';
}

.list-t-gift .button-type-content {
	min-height: 95px;
	padding: 12px 15px;
	line-height: normal;
	-webkit-box-sizing: border-box;
	box-sizing: border-box;
}

.list-t-gift .tit-gift {
	display: block;
	font-size: 20px;
	color: #000;
	letter-spacing: -0.5px;
	line-height: 1.2;
}

.list-t-gift .option {
	display: block;
	margin-top: 7px;
	color: #959595;
	font-size: 12px;
}

/* Swiper */
